<template>
  <div class="header">
  	<div class="content-wrapper">
  		<div class="avatar">
  			<img :src="seller.avatar" width="64" height="64">
  		</div>
  		<div class="content">
  			<div class="title">
  				<span class="brand"></span>
  				<span class="name">{{seller.name}}</span>
  			</div>
  			<div class="description">
  				{{seller.description + ' / ' + seller.deliveryTime + '分钟送达'}}
  			</div>
  			<div class="support" v-if="seller.supports">
  				<span class="icon" :class="iconClassMap[seller.supports[0].type]"></span>
  				<span class="text">{{seller.supports[0].description}}</span>
  			</div>
  		</div>
  		<div class="support-count" v-if="seller.supports" @click="showDetails()">
            <span class="count">{{seller.supports.length+'个'}}</span>
            <i class="icon-keyboard_arrow_right"></i>
        </div>
  	</div>
  	<div class="bulletin-wrapper" @click="showDetails()">
  		<span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
  		 <i class="icon-keyboard_arrow_right"></i>
  	</div>
  	<div class="background">
    <img :src="seller.avatar" width="100%" height="100%"/>
    </div>
    <transition name="fade">
    <div v-show="detailShow" class="detail">
    	<div class="detail-wrapper clearfix">
    		<div class="datail-main">
    			<h1 class="name">{{seller.name}}</h1>
    			<div class="star-wrapper">
	              <star :size="48" :score="seller.score"></star>
	            </div>
	            <div class="title">
	            	<div class="line"></div>
	            	<div class="text">优惠信息</div>
	            	<div class="line"></div>
	            </div>
	            <ul v-if="seller.supports" class="supports">
	            	<li class="support-item" v-for="item in seller.supports">
	            		<span class="icon" :class="iconClassMap[item.type]"></span>
	            		<span class="text">{{item.description}}</span>
	            	</li>
	            </ul>
	            <div class="title">
	              <div class="line"> </div>
	              <div class="text">商家公告</div>
	              <div class="line"></div>
	            </div>
	            <div class="bulletin">{{seller.bulletin}}</div>
    		</div>
    	</div>
    	<div class="detail-close" @click="hideDetail()">
    			<i class="icon-close"></i>
    		</div>
    </div>
    </transition>
  </div>
</template>

<script>
import star from '../star/star.vue'
export default {
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    star
  },
  data() {
    return {
      detailShow: false
    }
  },
  methods: {
    showDetails() {
      this.detailShow = true;
    },
    hideDetail() {
      this.detailShow = false;
    }
  },
  created() {
    this.iconClassMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*@import '../../common/stylus/icon'*/
.header {
	position: relative;
	background: rgba(7,17,27,0.5);
  color: #fff;
	overflow: hidden;
}
.content-wrapper {
	position: relative;
	padding: 24px 14px 18px 24px;
	font-size: 0;
}
.avatar {
	display: inline-block;
	vertical-align: top;
}
.avatar img {
	border-radius: 2px;
}
.content {
	display: inline-block;
	margin-left: 16px;
}
.title {
	margin: 2px 0 8px 0;
	font-size: 16px;
}
.brand {
	display: inline-block;
	vertical-align: top;
	width: 30px;
	height: 18px;
	background-image: url(img/brand@2x.png);
	background-size: 30px 18px;
	background-repeat: no-repeat;  
}
.name {
	margin-left: 12px;
	font-size: 16px;
	line-height: 18px;
	font-weight: bold; 
}
.description {
	font-size: 12px;
	margin-bottom: 10px;
}
.content .supports {
	display: inline-block;
}
.icon {
	display: inline-block;
	vertical-align: top;
	width: 12px;
	height: 12px;
	margin-right: 4px;
	background-size: 12px 12px;
	background-repeat: no-repeat;
}
.decrease {
	background-image: url(img/decrease_1@2x.png);
}
.discount {
	background-image: url(img/discount_1@2x.png);
}
.guarantee {
	background-image: url(img/guarantee_1@2x.png);
}
.invoice {
	background-image: url(img/invoice_1@2x.png);
}
.special {
	background-image: url(img/special_1@2x.png);
}
.text {
	line-height: 12px;
	font-size: 12px;
}
.support-count {
	position: absolute;
	right: 12px;
	bottom: 14px;
	padding: 0 8px;
	height: 24px;
	line-height: 24px;
	border-radius: 14px;
	background-color: rgba(0,0,0,0.2);
	text-align: center;
}
.count {
	vertical-align: top;
	font-size: 10px;
}
.icon-keyboard_arrow_right {
	font-size: 10px;
  margin-left: 2px;
  line-height: 24px;
}
.bulletin-wrapper {
	position: relative;
	height: 28px;
	line-height: 28px;
	padding: 0 22px 0 12px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	background: rgba(7,17,27,0.2);
}
.bulletin-title {
	display: inline-block;
	vertical-align: top;
	margin-top: 9px;
	width: 22px;
	height: 12px;
	background-image: url(img/bulletin@2x.png);
	background-size: 100% 100%;
	background-repeat: no-repeat;
}
.bulletin-text {
	margin: 0 4px;
	font-size: 10px;
}
.bulletin-text ..icon-keyboard_arrow_right {
	position: absolute;
	font-size: 10px;
	right: 12px;
	top: 8px;
}
.background {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	filter: blur(10px);
	z-index: -1;
}
.detail {
	position: fixed;
	top: 0px;
	left: 0px;
	z-index: 100;
	height: 100%;
	width: 100%;
	background: rgba(7,17,27,0.8);
}
.detail-wrapper {
	min-height: 100%;
	width: 100%;
}
.clearfix {
	 display: inline-block;
}
.clearfix:after {
	display: block;
  content: '';
  height: 0px;
  line-height: 0px;
  clear: both;
  visibility: hidden;
}
.datail-main {
	margin-top: 64px;
	padding-bottom: 64px; 
}
.name {
	margin-left: 6px;
	font-size: 16px;
	text-align: center;
	line-height: 18px;
	font-weight: bold;
}
.detail-close {
	position: relative;
	width: 32px;
	height: 32px;
	margin: -64px auto 0 auto;
	clear: both;
	font-size: 32px;
}

/*组件star样式*/
.star-wrapper {
	margin: 16px 11px 28px 0;
	text-align: center;
}
.detail-wrapper .title {
	display: flex;
	width: 80%;
	margin: 0px auto 24px auto;
}
.detail-wrapper .title .line {
	flex: 1;
	height: 1px;
	background: rgba(255,255,255,0.2);
	margin: auto;
}
.datail-main .title .text {
	padding: 0 12px;
	font-size: 14px;
	font-weight: 700;
}
.datail-main .supports {
	width: 80%;
	padding: 0 0 28px 36px;
}
.datail-main .supports .support-item {
	color: white;
	padding:0 6px 12px 12px;
}
.datail-main .supports .support-item .text {
	vertical-align: middle;
	font-size: 12px;
	font-weight: 200;
	color: rgb(255,255,255);
	line-height: 16px;
}
.datail-main .supports .support-item .icon {
	display: inline-block;
	vertical-align: top;
	width: 16px;
	height: 16px;
	margin-right: 6px;
	background-repeat: no-repeat;
}
.datail-main .supports .support-item .decrease {
	background-image: url(img/decrease_2@2x.png);
}
.datail-main .supports .support-item .discount {
	background-image: url(img/discount_2@2x.png);
}
.datail-main .supports .support-item .guarantee {
	background-image: url(img/guarantee_2@2x.png);
}
.datail-main .supports .support-item .invoice {
	background-image: url(img/invoice_2@2x.png);
}
.datail-main .supports .support-item .special {
	background-image: url(img/special_2@2x.png);
}
.datail-main .bulletin {
	padding: 0 48px;
	font-size: 12px;
	font-weight: 200;
	color: rgb(255,255,255);
	line-height: 24px;
}
/*动画过度*/
.fade-enter-active,.fade-leave-active {
	transition: opacity .5s
}
.fade-enter,.fade-leave-active {
	 opacity: 0
}
</style>



