<template>
  <div>
     <top :seller="seller"></top>
    <div id="tab">
        <div class="tab-item">
          <router-link to="/goods">商品</router-link>
        </div>
        <div class="tab-item">
          <router-link to="/ratings">评论</router-link>
        </div>
        <div class="tab-item">
          <router-link to="/seller">商家</router-link>
        </div>
    </div>
    <router-view :seller="seller"></router-view>
  </div>
</template>

<script>
import top from './components/header/header'
import axios from 'axios'

export default {
  data() {
    return {
      seller: {}
    }
  },
  created() {
    axios.get('static/data.json').then((res) => {
      this.seller = res.data.seller
    })
  },
  components: {
    top
  }
}
</script>

<style lang="stylus">
@import 'common/stylus/index.styl'
#tab {
  display:flex;
  width:100%;
  height: 40px;
  line-height:40px;
  border-bottom: 0.025rem solid rgba(7,17,27,0.1);
}
.tab-item {
  flex: 1;
  text-align: center;
}
.tab-item {
  display:block;
  font-size: 14px;
  color: rgb(77,85,93);
}
.tab-item a{
  display: block;
  font-size: 14px;
}
.active {
  color: rgb(240, 20, 20);
}
</style>



